.how-it-works.row .col-2::after {
  content: "";
  position: absolute;
  border-left: 3px solid #2780E3;
  z-index: 1;
}
.how-it-works.row .col-2.bottom::after {
  height: 50%;
  left: 50%;
  top: 50%;
}
.how-it-works.row .col-2.full::after {
  height: 100%;
  left: calc(50% - 3px);
}
.how-it-works.row .col-2.full.left-item::after {
  left: 50%;
}
.how-it-works.row .col-2.top::after {
  height: 50%;
  left: 50%;
  top: 0;
}
.how-it-works.row .col-2.last-item::after {
  height: 50% !important;
  top: 0;
}
